.home{
    display: flex;
    height: 100%;
    justify-content: center;
    align-items: center;
}
.hero-text{
    color:white;
    text-align: center;
    font-size: 3.6rem;
}
span{
    color: #037FFF;
}
.subtext{
    text-align: center;
}
.icons{
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1.5rem 0;
    .icon-holder{
        margin-right: 1rem;
        .icon{
           font-size: 2.8rem;
           color: #a4acc4;
           transition: all .4s ease-in-out;
        }
        .fb:hover{
            color: blue;
        }
        .gh:hover{
            color: rgba(212, 45, 212, 0.788);
        }
        .ld:hover{
            color: red;
        }
    }
}