.projects-container{
    margin-top:10rem;
    display: flex;
    justify-content: space-between;
    position: relative;
    @media screen and (max-width:1400px){
        flex-direction: column;
        margin-bottom: 2rem;
    }
}
.project-content img{
    width:360px;
    height: 350px;
    border-radius: 1rem;
}
.project-content img:hover{
    transform: scale(1.05);
}
.p-title{
    text-align: center;
    font-size: 1.5rem;
    margin-bottom: 1.5rem;
    letter-spacing: 2px;
    @media screen and (max-width:1400px){
       text-align: left;
    }
}
.project-content h5{
    margin-top:2rem;
}
.project-content a{
    text-decoration: none;
    letter-spacing: 3px;
    color:white;
}
.project-content{
    position: relative;
}
.button{
    width: 50%;
    margin:0px auto;
    @media screen and (max-width:1400px){
        margin-bottom: 2rem;
        width: 100%;
    }
}
